@import '../../sass/utils/mixin';
.vectorImg {
  @include tablet_max {
    padding-top: 50px;
  }
  @include tablet {
    padding-top: 100px;
  }
  @include desktop {
    padding-top: 66px;
  }

  height: calc(100vh - 71px);
  background-repeat: no-repeat;
  background-position: bottom;
  background-size: contain;
  @include tablet {
    background-image: url(../../assets/images/vector-tablet.png);
  }

  @include desktop {
    background-image: url(../../assets/images/vector-desktop.png);
  }
}
